html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

.btn {
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  transition: all 0.3s;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background: transparent;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: "Panton Regular",sans-serif;
  font-size: 22px;
  padding: 10px 35px 15px;
  text-align: center;
  text-decoration: none;
  touch-action: manipulation;
  vertical-align: middle;
  white-space: nowrap;
  font-weight: 400;
}

.btn input,
.btn textarea,
.btn select {
  outline: none;
}

.btn-contact {
  background: #fff;
  border: 1px solid #333;
  padding: 0.5em 0.8em;
  border-radius: 0.3em;
  cursor: pointer;
  transition: 1s;
}

.btn-contact:hover {
  background: #28ead7;
  transition: 0.5s;
}

.btn:hover,
.btn:focus {
  background-color: #ccc;
  color: #fff;
}

.btn::after {
  background: url("/build/img/2016/arrow.svg") no-repeat top right;
  content: '';
  display: block;
  height: 9px;
  position: static;
  width: 100%;
}

.btn:disabled {
  cursor: not-allowed;
  opacity: .5;
}

.btn:disabled:hover {
  background-color: #fff;
}

.btn--primary {
  background: #4d6f85;
}

.btn--primary:hover,
.btn--primary:focus {
  background-color: #364e5d;
  color: #fff;
}

.btn--secondary {
  background: #28ead7;
}

.btn--secondary:hover,
.btn--secondary:focus {
  background-color: #20bbac;
  color: #fff;
}

